
.session-content {
  margin: 30px 30px 0 130px;
}
.master-card-header {
  background: #FFFFFF;
  border-radius: 5px 5px 0 0;
}
.master-card-picture {
  border-radius: 5px 0 0 0;
}
.master-card-footer .cell select,
.master-card-footer .cell input,
.master-card-footer .cell button {
  margin: 0;
}

.main-menu .main-menu-icon {
  max-width: 40px;
  max-height: 40px;
}
@media (max-width: 40em), (orientation: portrait) {
.session-content {
    margin: 30px 20px 100px;
}
.main-menu {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: auto;
    text-align: center;
    z-index: 10;
    margin: 0;
    padding: 0;
    border: none;
    top: unset;
}
.main-menu ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-align: stretch;
        -ms-flex-align: stretch;
            align-items: stretch;
}
.main-menu ul li {
    padding: 10px 5px;
    height: auto;
    display: block;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    width: unset;
    margin: 0;
    font-size: 12px;
    border: none;
}
.main-menu ul li label {
    font-size: 11px;
}
.main-menu .main-menu-icon {
    max-width: 20px;
    max-height: 20px;
}
.main-menu.submenu-shown {
    width: 100%;
    overflow: auto;
    height: auto;
}
.main-menu ul.submenu {
    position: static;
    width: 100%;
    overflow: auto;
    height: unset;
}
.main-menu ul.submenu li {
    text-align: center;
    display: block;
    font-size: 1rem;
}
}
